import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { Card, Form, Input, Row, Col, Checkbox, message } from 'antd';
import { requiredRule } from 'utils/validateRules';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import InputMoney from 'components/common/input/InputMoney';
import {
  getAccountsReceivableById,
  createAccountReceivale,
  updateAccountReceivable,
} from 'services/accountsReceivable';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
    isFee: false,
    isTrainExpense: false,
    isRestudy: false,
    isRetest: false,
    amount: 0,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormAccountsReceivable' })
class AccountsReceivableEdit extends Component {
  state = {
    id: '',
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ id: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getAccountsReceivableById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    try {
      const { id } = this.state;
      if (!id) {
        await createAccountReceivale(values);
        message.success('Tạo mới thành công');
      } else {
        await updateAccountReceivable({ id, ...values });
        message.success('Cập nhật thành công');
      }
      this.props.history.goBack();
    } catch (err) {
      console.log(err);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const formItemCheckboxLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 20 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      colon: false,
    };
    return (
      <Card title="Thêm mới khoản thu">
        <Row>
          <Col span={24}>
            <Form onSubmit={this.handleSubmit}>
              <Form.Item label="Tên" {...formItemLayout}>
                {getFieldDecorator('label', {
                  initialValue: initialValue.label,
                  rules: [requiredRule],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Số tiền" {...formItemLayout}>
                {getFieldDecorator('amount', {
                  initialValue: initialValue.amount,
                  rules: [requiredRule],
                })(<InputMoney style={{ width: '100%' }} />)}
              </Form.Item>
              <Row>
                <Col offset={5} span={12}>
                  <Col span={6}>
                    <Form.Item
                      {...formItemCheckboxLayout}
                      style={{ display: 'inline-block' }}
                      label="Học phí"
                    >
                      {getFieldDecorator('isFee', {
                        valuePropName: 'checked',
                        initialValue: initialValue.isFee,
                      })(<Checkbox />)}
                    </Form.Item>
                  </Col>
                  <Col span={6}>
                    <Form.Item
                      {...formItemCheckboxLayout}
                      style={{ display: 'inline-block' }}
                      label="Kinh phí ĐT"
                    >
                      {getFieldDecorator('isTrainExpense', {
                        valuePropName: 'checked',
                        initialValue: initialValue.isTrainExpense,
                      })(<Checkbox />)}
                    </Form.Item>
                  </Col>
                  <Col span={6}>
                    <Form.Item
                      {...formItemCheckboxLayout}
                      style={{ display: 'inline-block' }}
                      label="Học lại"
                    >
                      {getFieldDecorator('isRestudy', {
                        valuePropName: 'checked',
                        initialValue: initialValue.isRestudy,
                      })(<Checkbox />)}
                    </Form.Item>
                  </Col>
                  <Col span={6}>
                    <Form.Item
                      {...formItemCheckboxLayout}
                      style={{ display: 'inline-block' }}
                      label="Thi lại"
                    >
                      {getFieldDecorator('isRetest', {
                        valuePropName: 'checked',
                        initialValue: initialValue.isRetest,
                      })(<Checkbox />)}
                    </Form.Item>
                  </Col>
                </Col>
              </Row>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <AccountsReceivableEdit {...props} />
  </Page>
));



